/* src/components/CategoryFilter.css */
.category-filter {
    margin-bottom: 1rem;
    text-align: center;
  }
  
  .category-nav {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
  }
  
  .category-button {
    background: #2c2c2c; /* Darker background for buttons */
    border: 1px solid #444; /* Slightly lighter border for contrast */
    border-radius: 4px;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    color: #ccc; /* Light text color for readability */
    cursor: pointer;
    transition: background 0.3s ease, color 0.3s ease;
  }
  
  .category-button:hover {
    background: #3a3a3a; /* Slightly lighter on hover */
  }
  
  .category-button.active {
    background: #007bff; /* Active button background color */
    color: #fff; /* White text color for active button */
    border-color: #007bff; /* Border color matches the active background */
  }
  
  .category-button:focus {
    outline: none;
    box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.5); /* Blue shadow for focus */
  }
  